  get_chart2_data() {
    let arr = this.STATES_AGENT.slice(
      this.times,
      this.times * 1 + this.num * 1
    );
    let xArr: any[] = [];
    let series: any = [
      {
        name: "act_id",
        type: "line",
        data: []
      }
    ];
    arr.map((val: any[]) => {
      xArr.push("");
      series[0].data.push(val[1]);
    });

    return {
      tooltip: {},
      xAxis: {
        data: xArr
      },

      yAxis: {
        min: -1,
        max: 5,
        axisLabel: {
          formatter: function(value: number) {
            if (value < 0) {
              return "待定";
            }
            let arr = ["零档", "一档", "二档", "三档", "四档", "五档"];
            return arr[value];
          }
        }
      },
      series: series
    };
  }
  /**检测哪些分类有超过0.3的数据 */
  check_data() {
    this.dataArr.map((val: any) => {
      val.style["background-color"] = "greenyellow";
      // console.log(val.data.length);
      let times = this.times * 1 + this.num * 1;
      if (val.data.length > times) {
        let data = val.data[times];
        for (let i = 1; i < data.length; i++) {
          data[i] > 0.3 ? (val.style["background-color"] = "#ce4e4e") : "";
        }
        // console.log(val.data[this.times])
