  nav_click(i: number) {
    this.index = i;
    for (var j = 0; j < this.dataArr.length; j++) {
      this.dataArr[j].style["border-color"] = "#868686";
    }
    this.dataArr[i].style["border-color"] = "rgb(226, 209, 55)";

    // this.myChart.setOption(this.get_chart_data_all());
    this.myChart.clear();
  }
  get_chart_data() {
    if (this.times * 1 + this.num * 1 > this.dataArr[this.index].data.length) {
      clearInterval(this.setId);
      alert("数据已经加载完毕");
    }
    let arr = this.dataArr[this.index].data.slice(
      this.times,
      this.times * 1 + this.num * 1
    );

    let xArr: any[] = [];
    let series: any[] = [];
    arr.map((val: any) => {
      // xArr.push(((1+val[0]) / 1000).toString());
      xArr.push("");
      for (var i = 1; i < val.length; i++) {
        if (series.length < i) {
          series.push({
            name: "hs_" + i,
            type: "line",
            data: []
          });
        }
        series[i - 1].data.push(val[i]);
      }
    });
    console.log("series", series.length);
    this.times++;
    return {
      tooltip: {},
      xAxis: {
        data: xArr,
        name: "时间"
      },

      yAxis: {
        min: 0.3,
        max: 1,
        name: "警告线"
      },
      series: series
    };
  }
